body {
  font-family: 'Raleway', sans-serif;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  min-width: 100vw;
  margin: 0%;
}

.header {
  height: 20vh;
  background-color:rgba(60, 110, 113, 1) ;
  font-size: xx-large;
  color: aliceblue;
  display: flex;
  justify-content: right;
  width: 100vw;
}
 
.horizontal-card {
  height: 30vh;
  width: 80vw;
  box-shadow: 2px 2px 2px rgb(217, 214, 214)
  ;
  display: flex;
  justify-content: center;
  background-color: rgb(246, 248, 248);
  margin-left: 10%;
  margin-right: 10%;
  justify-content: space-around;
  align-items: center;
  padding: 2%;
}

.vertical-card{
  height: auto;
  width: auto;
  box-shadow: 5px 5px 5px rgb(217, 214, 214)
  ;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(246, 248, 248);
  margin-left: 2%;
  margin-right: 2%;
}

.sections{
  display: flex;
  flex-direction: column;
  align-items: center;
}

img{
  height: 100px;
  width: 100px;
}

h2 {
  text-align: center;
  font-size: xx-large;
}

.next{
  display: flex;
  flex-direction: row;
  padding-left: 8%;
  padding-right: 8%;

}

.biopic{
  margin: 2%;
  border-radius: 50% 50%;
  height: 200px;
  width: 200px;
}

.logos{
  width: 100%;
  height: auto;
}

a:hover {
  color: #82846D;
  /* color: #714955; */
  /* color: #80727B; */
  transition: 0.3s;
  transform: scale(1.03, 1.03);
}

a:link {
  color: black;
  text-decoration: none;
}

a:visited {
  color: black;
}
